@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
.sidebar {
    position: sticky;
    top: 0;
    bottom: 0;
    height: 95vh;
    padding-left: 2%;
    padding-right: 2%;
    padding-top: 3vh;
    padding-bottom: 2vh;
    width: 9%;
    display: flex;
    /* font-size: 14px; */
    font-size: 0.8vw;
    flex-direction: column;
    justify-content: space-between;
}

.logo_sidebar {
    margin-top: 3vh;
    width: 80%;
}

.sidebar_section_header {
    font-weight: bolder;
    font-size: 0.7vw;
    margin-bottom: 1.5vh;
    color: var(--nofocus-font-grey);
}

.menu_sidebar {
    width: 90%;
    height: 20vh;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
}

.menu_link{
    color: var(--nofocus-font-grey);
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.menu_link > svg {
    margin-right: 10px;
    margin-top: -5px;
    width: 20px !important;
}

.menu_active {
    color: var(--header-font-white);
}

.categories_sidebar {
    display: flex;
    flex-direction: column;
    height: 30%;
    justify-content: space-between;
    font-weight: 300;
}

.category {
    color: var(--nofocus-font-grey);
    display: flex;
    align-items: center;
    cursor: pointer;
}

.category_active {
    color: var(--header-font-white);
}

.category_active_dash {
    height: 2px;
    width: 8%;
    background-color: var(--logo-green);
    margin-right: 8%;
    margin-top: -3%;
}

.sidebar_footer{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-size: 0.6vw;
}
.light_green {
    color: var(--light-hyperlink-green);
}

.sidebar_footer_text > .dark_green {
    color: var(--dark-hyperlink-green)
}

.sidebar_footer > * {
    margin-bottom: 3%;
}
.header {
  width: 100%;
  height: 4vh;
  margin-bottom: 4vh;
  display: flex;
  justify-content: space-between;
  flex-wrap: nowrap;
}

.search {
  width: 73%;
  display: flex;
  border: none;
  align-items: center;
  justify-content: space-between;
  color: var(--header-font-white);
  background-color: #1a1b20;
  border-radius: 8px;
  padding: 0.4%;
}

.search_input_container {
  display: flex;
  width: 80%;
  align-items: center;
}

.search_category_container {
  display: flex;
  flex-direction: row;
  height: 100%;
  align-items: center;
}

.search_seperator {
  width: 2px;
  height: 60%;
  background-color: var(--logo-green);
}

.search_category {
  margin-right: 16px;
  margin-left: 12px;
  color: var(--nofocus-font-grey);
  font-family: Arial, Helvetica, sans-serif;
  letter-spacing: 0.04vw;
  font-size: 0.8vw;
}

.search_input {
  background-color: inherit;
  color: var(--header-font-white);
  width: 90%;
  margin-left: 2%;
  margin-bottom: 0;
  border: none;
  font-size: 1vw;
}

.header_navcard {
  padding-left: 1%;
  padding-right: 1%;
  width: 22%;
  align-items: center;
  color: var(--header-font-white);
  background-color: var(--searchbar-black);
  display: flex;
  justify-content: space-between;
  flex-wrap: nowrap;
  border-radius: 8px;;
}

.header_username {
  font-size: 0.9vw;
  overflow: hidden;
  text-overflow: ellipsis;
}

.dropdown {
  cursor: pointer;
  position: absolute;
  background-color: var(--searchbar-black);
  top: 11vh;
  width: 9vw;
  margin-left: 9vw;
  border-radius: 0.5vw;
}

.dropdown_option {
  height: 32px;
}

.dropdown_option:hover {
  background-color: #30313a;
}

.dropdown_first {
  border-radius: 8px 8px 0 0;
}

.dropdown_last {
  border-radius: 0 0 8px 8px;
}

.dropdown_option {
  height: 32px;
  width: calc(100% - 12px);
  padding-left: 12px;
  color: var(--header-font-white);
  font-size: 0.8vw;
  display: flex;
  align-items: center;
}

.dropdown_option > svg {
  color: var(--nofocus-font-grey);
  margin-right: 5%;
}

.settings_link {
  display: flex;
  align-items: center;
  justify-content: center;
}

.settings_link > svg {
  width: 20px !important;
  margin-bottom: 4px;
}
.home {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
}

.products {
  width: 73.8%;
  display: flex;
  flex-direction: column;
}

.home_header_text {
  display: flex;
}

.review_header_text {
  font-size: 1.5vw;
  display: flex;
  justify-content: space-between;
  padding-bottom: 3vh;
}

.home_header_category {
  color: var(--logo-green);
}

.products_area {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}

.products_area::after {
  content: "";
  flex: none;
  width: 31%;
}

.product_card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 40vh;
  width: 25%;
  padding: 2.8%;
  border: 1px solid var(--nofocus-font-grey);
  margin-bottom: 4%;
  border-radius: 8px;
  cursor: pointer;
}

.product_card:nth-child(3) {
  margin-right: 0;
}

.product_card_image {
  width: 100%;
  height: 55%;
  position: relative;
}

.product_card_image > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.product_name {
  font-size: 1vw;
  color: var(--header-font-white);
  overflow: hidden;
  text-overflow: ellipsis;
}

.product_metadata {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  color: rgb(65, 65, 65);
  font-size: 1vw;
}
.product_metadata span {
  text-align: center;
  color: var(--header-font-white);
}
.product_metadata span sub {
  color: var(--logo-green);
}

.product_card_button_container {
  height: 10%;
  margin-top: 2%;
  width: 100%;
  display: flex;
  justify-content: center;
}

.add_to_cart_button {
  width: 100%;
  font-size: 0.8vw !important;
}

/* REVIEWS */
.user_reviews_area {
  width: 24%;
}

.user_reviews {
  width: 100%;
}

.user_reviews_buttons_area {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  /* margin-bottom: 5%; */
}

.reviews_button {
  cursor: pointer;
  font-size: x-small;
  float: right;
}

.reviews_button_ghost {
  width: 40%;
}

.reviews_card {
  display: flex;
  flex-direction: column;
  width: 90%;
  padding: 5%;
  margin-bottom: 5%;
  background-color: var(--searchbar-black);
  border-radius: 8px;
  cursor: default;
}

.reviews_card_info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.reviews_card_info_user,
.reviews_card_info_rating {
  font-size: 1vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
  text-overflow: ellipsis;
}

.reviews_card_info_date {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-size: 0.6vw;
}

.reviews_card_stars {
  display: flex;
  justify-content: flex-end;
  font-size: 0.8vw;
  color: var(--logo-green);
}

.reviews_card_review {
  font-size: 0.8vw;
  margin-top: 5%;
  overflow: hidden;
  text-overflow: ellipsis;
}

.reviews_card_review::first-letter {
  color: var(--logo-green);
  text-transform: uppercase;
  font-size: 1.4vw;
}

.product_page {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
}

.product_page_main {
  width: 73.8%;
  display: flex;
  flex-direction: column;
}

.product_page_top {
  display: flex;
  height: 60vh;
  flex-direction: row;
}

.product_page_image {
  width: 100%;
  height: 80%;
  position: relative;
}

.product_image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.product_page_images {
  width: 45%;
  padding-right: 5%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.product_images_carousel {
  display: flex;
  justify-content: left;
  height: 15%;
}

/* max 4 images */
.product_images_carousel > div:nth-child(n + 5) {
  display: none;
}

.carousel_image_container,
.carousel_image_container_active {
  width: 18%;
  border-radius: 16px;
  border: 2px solid;
  margin-left: 2.1vw;
}

.carousel_image_container {
  border-color: transparent;
  margin-left: 2.1vw;
}

.carousel_image_container_active {
  border-color: rgba(89, 186, 71, 1);
}

.static_carousel_image_container,
.static_carousel_image_container_active {
  width: 18%;
  border-radius: 16px;
  border: 2px solid;
}

.static_carousel_image_container {
  border-color: transparent;
}

.static_carousel_image_container_active {
  border-color: rgba(89, 186, 71, 1);
}

.carousel_image {
  object-fit: cover;
  border-radius: 12px;
  height: 100%;
  width: 100%;
}

.product_page_info {
  width: 50%;
  margin-right: 5%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.product_info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex: 1 1;
}

.product_page_info_top {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: space-between;
}

.product_title,
.product_overview_title {
  font-size: 1.4vw;
}

.product_overview_title {
  color: var(--logo-green);
  margin-top: 3%;
  margin-bottom: 3%;
}

.product_meta_data {
  height: 22%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.meta_data_card {
  height: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  width: 15%;
}

.meta_data_card > div {
  height: 48.5%;
  background-color: var(--searchbar-black);
  display: flex;
  align-items: center;
  justify-content: center;
}

.meta_title {
  border-radius: 10px 10px 0 0;
  font-size: 0.8vw;
}

.meta_contents {
  border-radius: 0 0 10px 10px;
}

.meta_variable {
  margin-right: 5%;
  font-size: 1vw;
  color: var(--logo-green);
}

.meta_unit {
  font-size: 0.6vw;
}

.product_quantity_container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 10%;
  padding-right: 10%;
}

.product_purchase {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 15%;
  margin-top: 5%;
}

.product_button {
  width: 60%;
}

.wishlist_button {
  width: 35%
}

.product_price {
  display: flex;
  font-size: 1.4vw;
}

.price_price {
  margin-left: 8px;
  color: var(--logo-green);
}

/*Reviews*/
.create_review {
  margin-top: 3vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.create_review_header {
  font-size: 0.8vw;
  display: flex;
  text-align: left;
  padding-left: 2%;
  width: 98%;
}

.add_to_reviews_input {
  width: 90%;
  color: var(--paragraph-font-grey);
  resize: none;
  background-color: var(--sidebar-black);
  padding: 4%;
  border-radius: 4px;
  border: 1px solid var(--logo-green);
  margin-bottom: 1.5vh;
}

.add_to_reviews_input:focus {
  border-width: 1px;
  border-style: solid;
  box-shadow: none;
  outline: none;
}

.reviews_header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.create_review_bottom {
  height: 3vh;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.create_review_rating {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}

.create_review_rating > svg {
  font-size: 1.4vw;
  color: var(--logo-green);
}

.create_review_button {
  width: 40%;
  height: inherit !important;
}

.product_title {
  overflow: hidden;
  text-overflow: ellipsis;
  width: 80%;
}

.product_quantity {
  display: flex;
  flex-direction: row;
  justify-content: right;
  width: 20%;
  min-width: 7vw;
  height: 100%;
  max-height: 44px;
  border-radius: 10px;
  background-color: var(--searchbar-black);
}

.product_quantity_input {
  background-color: transparent;
  border: none;
  color: inherit;
  width: 55%;
  display: flex;
  align-items: center;
  font-size: 16px;
  margin-bottom: 0;
  padding-left: 15%;
}

.quantity_arrows {
  max-height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.order_quantity_button {
  height: 40%;
  color: var(--logo-green);
}

.order_quantity_button:first-child {
  margin-top: 20%;
}

.order_quantity_button > svg {
  height: 80%;
  cursor: pointer;
}

.price_table {
  width: 100%;
  border-collapse: collapse;
  color: var(--paragraph-font-grey);
}

.price_table_row > *:not(:last-child) {
  width: 29%;
}

.price_table_row > th,
.price_table_row > td {
  text-align: center;
  border: 0.5px solid var(--paragraph-font-grey);
  padding: 0;
  border-spacing: 0;
}

.price_table_row > td {
  font-size: 0.8vw;
}

.price_table_row > th {
  font-size: 1vw;
  color: var(--header-font-white);
  padding-top: 4px;
}

.table_add_cart_icon {
  width: 20px !important;
  color: var(--light-hyperlink-green);
  margin-top: 1px;
  margin-bottom: -1px;
  cursor: pointer;
}

.no_priceTiers_message {
  font-size: 1vw;
}

.product_card_heart {
  position: absolute;
  display: flex;
  justify-content: flex-end;
  top: 0;
  right: 0;
  bottom: 0;
  margin: 5%;
  cursor: pointer;
  width: 8% !important;
  height: 8% !important;
  color: var(--logo-green);
}

.price_table_container {
  min-height: 35%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.counter_container{
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 0.5vh;
}

.character_counter{
  font-size: 0.75vw;
  margin: 0;
}
.account_container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.section_container {
  width: 48%;
}

.wishlist_container {
  margin-top: 2vh;
  display: flex;
  flex-direction: column;
}

.wishlist_product_card_container {
  width: 100%;
  margin: 1.3vh 0;
  position: relative;
}

.wishlist_product_card {
  display: flex;
  position: relative;
  height: 4vw;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 1vw;
  width: calc(100% - 2vw);
  background-color: var(--searchbar-black);
  border-radius: 8px;
}

.wishlist_product_card_image_container {
  margin-right: 3%;
  height: 100%;
  width: 17%;
}

.wishlist_product_image {
  object-fit: cover;
  width: 100%;
  height: 100%;
  border-radius: 16px;
}

.wishlist_product_name {
  font-size: 1.2vw;
  width: 50%;
  color: var(--header-font-white);
}

.wishlist_product_order_number {
  color: rgb(255, 255, 255, 30%);
}
.wishlists_empty_space {
  width: 10%;
}
.wishlist_product_button {
  margin-right: 5% !important;
  width: 25% !important;
  padding: 4px !important;
  border-radius: 3px !important;
  font-size: min(0.85vw, 16px) !important;
}

.remove_button_container {
  cursor: pointer;
  margin-top: -1vh;
  margin-right: -1vh;
  position: absolute;
  top: 0;
  right: 0;
}

.login {
  display: flex;
  position: relative;
  overflow: hidden;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 100vh;
  width: 100vw;
  background-color: var(--background-grey);
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.login_decoration {
  position: absolute;
  width: 30vw;
  height: 30vh;
  display: flex;
  flex-direction: row;
  margin-left: -1vw;
}

.decor_green {
  background-color: #59ba47;
  width: 3px;
}

.decor_black {
  background-color: #1a1c22;
  width: 8px;
}

.decor_white {
  background-color: #ffffff;
  width: 6px;
}

.decoration_left {
  -webkit-transform: skewX(-40deg);
          transform: skewX(-40deg);
  left: 0;
  bottom: 0;
}

.decor_one {
  height: 20%;
  margin-top: 22%;
  margin-right: 9%;
}

.decor_two {
  height: 100%;
  margin-top: 17%;
  margin-right: 5%;
}

.decor_three {
  height: 18%;
  margin-top: 22%;
  margin-right: 4%;
}

.decor_four {
  margin-top: 36%;
  margin-right: 15%;
}

.decor_container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-right: 9%;
}

.decor_five {
  height: 10%;
}

.decor_six {
  height: 80%;
}

.decor_seven {
  height: 15%;
  margin-top: 18%;
}

.decoration_right {
  right: 0;
  top: 0;
  -webkit-transform: skewX(-40deg);
          transform: skewX(-40deg);
}

.decor_ten {
  height: 30%;
  margin-left: 60%;
  margin-right: 4%;
}

.decor_eleven {
  height: 8%;
  margin-right: 12%;
  margin-top: 18%;
}

.decor_twelve {
  height: 45%;
  margin-right: 8%;
}

.decor_thirteen {
  height: 100%;
  margin-right: 8%;
}

.decor_fourteen {
  height: 10%;
  margin-top: 52%;
}

.login_body {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.login_logo {
  width: 240px;
}

.login_wholesale {
  font-family: 'Bebas Neue';
  width: 100%;
  text-align: center;
  font-size: 30px;
  letter-spacing: 3px;
  align-items: center;
}

.google_login_button {
  text-decoration: none;
  color: darkslategrey;
  background-color: whitesmoke;
  box-shadow: 0 8px 16px rgba(14, 14, 14, 0.2);
  border-radius: 6px;
  padding: 10px;
  text-shadow: 0px 0px 4px white;
  font-size: 18px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: 500;
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 200px;
}

.google_icon {
  width: 10%;
}

.login_footer {
  margin-bottom: 16px;
  font-size: 0.8vw;
}

.login_form {
  margin-bottom: 10vh;
  display: flex;
  flex-direction: column;
  width: 30vw;
}

.auth_field_container {
  display: flex;
  flex-direction: row;
  align-items: center;
  background-color: var(--searchbar-black);
  width: 100%;
  border-radius: 6px;
  margin-bottom: 7%;
  box-shadow: 0 0 5px 0.1px var(--logo-green);
}

.auth_icon {
  width: 8% !important;
  padding: 2%;
}

.auth_input {
  width: 85%;
  border: none;
  background-color: rgba(0, 0, 0, 0);
  color: var(--header-font-white);
  font-size: 1.1vw;
}

.swal_text{
  color: rgb(180, 178, 178)!important;
}
.swal_confirm_button{
  background-color: rgb(46,125,50) !important;
}

.register_text {
  -webkit-transform: translateY(-20vh);
          transform: translateY(-20vh);
}
.cart_page {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.cart {
  width: 60%;
}
.cart_body {
  margin-top: 2vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.custom_orders {
  width: 30%;
}

.custom_orders_body {
  margin-top: 2vh;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.top_cart_area {
  padding-left: 2%;
  font-size: 2vw;
  margin-bottom: 2vh;
}

.checkout_button_container {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}

.checkout_button {
  width: 30%;
  letter-spacing: 1px !important;
}

.pay_button {
  width: 100%;
  letter-spacing: 1px !important;
}
/** ORDER CARD **/
.cart_products {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.order_card {
  display: flex;
  position: relative;
  padding: 1.5vw;
  flex-direction: row;
  align-items: center;
  background-color: var(--searchbar-black);
  margin-bottom: 2vh;
  border-radius: 20px;
  height: 12vh;
  width: 48vw;
}

.custom_order_card {
  display: flex;
  position: relative;
  padding: 1.5vw;
  flex-direction: column;
  align-items: center;
  background-color: var(--searchbar-black);
  margin-bottom: 2vh;
  border-radius: 20px;
}

.order_card_image_container {
  margin-right: 1.5vw;
  height: 100%;
  width: 20%;
}

.order_card_image {
  object-fit: cover;
  width: 100%;
  height: 100%;
  border-radius: 16px;
}

.order_card_contents {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
}

.custom_order_card_contents {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
}

.order_contents {
  width: 60%;
}

.custom_order_contents {
  width: 30%;
}

.order_card_product_name {
  cursor: pointer;
  font-size: 1.4vw;
  width: 100%;
  margin-bottom: 2%;
}

.custom_order_card_product_name {
  font-size: 1.4vw;
  width: 100%;
  margin-bottom: 2%;
}

.order_card_id {
  color: var(--nofocus-font-grey);
  font-size: 0.8vw;
}

.custom_order_card_id {
  color: var(--nofocus-font-grey);
  font-size: 0.8vw;
}

.order_card_quantities {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  width: 30%;
  align-items: center;
}

.custom_order_card_quantities {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 100%;
  width: 30%;
  align-items: center;
}

.custom_order_products {
  margin: 15%;
}

.remove_from_cart_text {
  color: rgb(255, 86, 86);
  font-size: 0.8vw;
  font-weight: 100;
  font-family: Arial, Helvetica, sans-serif;
  letter-spacing: 0.5px;
  cursor: pointer;
}

.remove_from_cart_text:hover {
  color: var(--attention-red);
}

.cart_quantity {
  display: flex;
  flex-direction: row;
  justify-content: right;
  width: 50%;
  border-radius: 10px;
  height: 35%;
  background-color: var(--background-grey);
}

/*** Unavailable Order Card ***/

.unavailable_order_card {
  display: flex;
  position: relative;
  padding: 1.5vw;
  flex-direction: row;
  align-items: center;
  background-color: rgba(26, 27, 32, 0.5);
  margin-bottom: 2vh;
  border-radius: 20px;
  height: 14vh;
}

.unavailable_order_card_image {
  object-fit: cover;
  width: 100%;
  height: 100%;
  border-radius: 16px;
  opacity: 0.5;
}

.unavailable_order_card_product_name {
  font-size: 1.4vw;
  width: 100%;
  margin-bottom: 2%;
  opacity: 0.5;
}

.unavailable_order_card_id {
  color: var(--nofocus-font-grey);
  font-size: 0.8vw;
  opacity: 0.5;
}

.unavailable_order_card_remove_area {
  color: rgb(255, 86, 86);
  font-size: 0.8vw;
  font-weight: 100;
  font-family: Arial, Helvetica, sans-serif;
  letter-spacing: 0.5px;
}

.unavailable_order_card_remove_area_text {
  opacity: 0.8;
}

.unavailable_order_card_remove {
  color: rgb(255, 86, 86);
  font-size: 1vw;
  font-weight: 100;
  font-family: Arial, Helvetica, sans-serif;
  letter-spacing: 0.5px;
  cursor: pointer;
  margin-top: 4vh;
  text-align: right;
}

.unavailable_order_card_remove:hover {
  color: var(--attention-red);
}

/*** Buy ***/

.buy {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.payment_header {
  font-size: 1.6vw;
  margin-bottom: 5%;
}

.gc_input {
  width: 100%;
  margin-top: 1vh;
  margin-bottom: 1vh;
}

.gcpay_form_area {
  width: 45%;
  background-color: var(--searchbar-black);
  border-radius: 16px;
  padding: 4%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.gc_form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin-bottom: 5%;
}

.gc_info_card {
  width: calc(100% - 2vw);
  border-radius: 8px;
  padding: 1vw;
  margin-bottom: 5%;
  background-color: var(--searchbar-black);
}

.cart_button_area {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.confirm_order {
  width: 20%;
  display: flex;
  flex-direction: column;
}

.err {
  color: var(--attention-red);
}

/** products in payment area **/
.cart_products_payment {
  width: 45%;
}
.gc_checkout_button {
  width: 100%;
  letter-spacing: 1px !important;
}

.gc_checkout_button:last-child {
  margin-left: 1vw;
}

.empty_cart {
  margin-top: 1vh;
}

.settings_container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.edit_account_container {
  width: 80%;
  margin: 2vh auto 3vh auto;
}

.edit_auth_field {
  margin: 1.90vh 0 1.50vh 0;
}

.edit_header {
  cursor: pointer;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.settings_payment_address {
  display: flex;
  flex-direction: column;
  text-align: right;
  margin-bottom: 1vw;
}

.settings_payment_addr_info {
  font-size: 1vw;
}
.dropdown_active {
  color: var(--logo-green);
}

.edit_address_input {
  width: 100%;
  margin-top: 2vh;
  margin-bottom: 2vh;
}

.edit_address_input:last-child {
  margin-bottom: 4vh;
}

.logout_button {
  margin-top: 34vh !important ;
}

.settings_button {
  margin-top: 3vh !important;
}

.settings_text {
  margin-top: 2vh;
}
.order_history {
  margin-top: 2vh;
  display: flex;
  flex-direction: column-reverse;
}

.getReceiptButton:link {
  color: var(--nofocus-font-grey);
}

.getReceiptButton:first-child {
  margin-right: 1vw;
}

.order_history_card {
  width: 90%;
  border-radius: 16px;
  padding: 5%;
  background-color: var(--searchbar-black);
  margin-bottom: 5%;
  cursor: default;
  display: flex;
  justify-content: space-between;
}

.order_history_left {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 10vh;
  width: 68%;
}

.order_history_number {
  font-size: 1.2vw;
  margin-left: 5px;
}

.order_history_right {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.order_history_content {
  font-size: 1vw;
  display: flex;
  align-items: baseline;
}

.order_history_content:not(:last-child):not(:first-child) {
  margin-bottom: 1vh;
}

.order_history_status {
  margin-top: 1vh;
}

.order_history_content_small {
  font-size: 0.6vw;
  margin-bottom: 1vh;
}

.order_history_split {
  display: flex;
  justify-content: space-between;
}

.order_history_split:first-child {
  margin-bottom: 2vh;
}

/* EACH ORDER PAGE */
.order_page {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.order {
  width: 55%;
}

.single_order {
  width: 90%;
  display: flex;
  flex-direction: column;
  margin-left: 2.5%;
  padding: 2.5%;
  border: 1px solid var(--nofocus-font-grey);
}

.single_order_header_top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.single_order_id {
  font-size: 1vw;
}

.single_order_total {
  font-size: 1vw;
  margin-top: 0.8vh;
}

.single_order_header_bottom {
  border-top: 1px solid var(--nofocus-font-grey);
  border-bottom: 1px solid var(--nofocus-font-grey);
  padding-bottom: 2vh;
  margin-bottom: 2vh;
  padding-top: 2vh;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.single_order_buttons {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-left: 1vw;
}

.single_order_button {
  width: 10vw;
  font-size: min(0.85vw, 16px) !important;
}

.order_again_button {
  margin-left: 20px !important;
}

/* PRODUCT IN ORDER */
.order_details_card {
  display: flex;
  position: relative;
  padding: 1.5vw;
  flex-direction: row;
  align-items: center;
  background-color: var(--searchbar-black);
  margin-bottom: 2vh;
  border-radius: 20px;
  height: 8vh;
}

.order_details_card_image_container {
  margin-right: 1.5vw;
  height: 100%;
  width: 10%;
}

.order_details_card_image {
  object-fit: cover;
  width: 100%;
  height: 100%;
  border-radius: 16px;
}

.order_product_fields_container {
  margin-bottom: 1vh;
  padding-left: 1.5vw;
  padding-right: 1.5vw;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.order_product_fields {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  width: 18vw;
}

.order_product_fields > div {
  width: 30%;
  text-align: center;
  font-size: 0.8vw;
}

.product_in_order_card_right_side {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.product_in_order_card_values {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  width: 18vw;
}

.product_in_order_card_values > div {
  width: 30%;
  text-align: center;
  font-size: 1vw;
}

.product_in_order_card_not_available {
  margin-top: 1vw;
  text-align: right;
  color: var(--attention-red);
  font-size: 1vw;
}
.product_in_order_card_quantity,
.order_product_field_units {
  border-left: 1px solid var(--nofocus-font-grey);
  border-right: 1px solid var(--nofocus-font-grey);
}

/* ORDER INFORMATION */
.order_info {
  width: 40%;
  display: flex;
  flex-direction: column;
}

.order_info_card {
  width: calc(100% - 2vw);
  border-radius: 8px;
  padding: 1vw;
  margin-bottom: 3%;
  background-color: var(--searchbar-black);
}

.order_info_title {
  font-size: 1.4vw;
  margin-top: 1vh;
  padding-bottom: 1vh;
  border-bottom: 1px solid var(--nofocus-font-grey);
}

.order_info_content {
  margin-top: 2vh;
  font-size: 1vw;
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: left;
}
.order_info_message {
  margin-left: 0.9vw;
  font-size: 1.2vw;
  position: relative;
  display: inline-block;
}

.order_info_message .order_info_pay_status {
  font-size: 1vw;
  visibility: hidden;
  width: 15vw;
  background-color: var(--sidebar-black);
  text-align: center;
  padding: 50% 50%;
  opacity: 0.9;
  border-radius: 6px;
  position: absolute;
  z-index: 1;
  top: -10vh;
  left: -15vw;
}

.order_info_message:hover .order_info_pay_status  {
  visibility: visible;
}

.order
.order_info_carrier {
  margin-top: 2vh;
}


.order_info_address {
  margin-top: 1vh;
  font-size: 1vw;
}

.order_info_address:nth-child(2) {
  margin-top: 2vh;
}

.order_info_split {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.invoice_container {
  width: 10vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.tracking_link {
  color: var(--dark-hyperlink-green);
}

.tracking_link:hover {
  color: var(--logo-green);
}


.MuiTableCell-alignRight {
    text-align: left !important;
}

.RaFileInput-removeButton-48:nth-child(n+5) {
    display: none;
}

